import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { GoodsList2Page } from '../goods-list2/goods-list2';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  iconBtn=[]
  recommends=[]
  list=[]
  constructor(public navCtrl: NavController) {
    for(let i=0;i<=3;i++){
      this.iconBtn.push({});
    }

    for(let i=0;i<=12;i++){
      this.recommends.push({});
    }

    for(let i=0;i<=13;i++){
      this.list.push({});
    }
  }
  
  openGoodsList2(){
    this.navCtrl.push(GoodsList2Page)
  }

}



