import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';

@Component({
  selector: 'page-about',
  templateUrl: 'about.html'
})
export class AboutPage {
  recommends=[]
  constructor(public navCtrl: NavController) {
    for(let i=0;i<=12;i++){
      this.recommends.push({});
    }
  }
}



